import React, { Component } from 'react';
import AccessoryCard from "./../AccessoryCard/AccessoryCard.jsx";
import { Redirect } from 'react-router-dom';
import agent from '../../services/agent.js';
import Dialog from 'react-bootstrap-dialog'
import Loader from 'react-loader-spinner'
import "./AddAccessories.css"

class AddAccessories extends Component {

  constructor(props){
      super(props);
      this.state = {...this.props.location.state.data};
      this.state.selectedAccessories = []
      this.state.renderShipping = false;
      this.state.accessories = []
  }

  endOverlay = () => {
    if(this.state.overlay){
      this.setState({overlay:false})
      document.getElementById("overlay").style.display = "none";
    }
  } 

  initOverlay = (message) => {
      if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
      }
  }

  componentDidMount = () => {
    this.initOverlay("Fetching the accessories data");
    this.fetchAllAccessoriesServer();
  }

  fetchAllAccessoriesServer = () => {
      agent.PCConfig.listAccessory()
      .then(response => {
          this.endOverlay();
          if(response.data.status == 1){
              let accArr = [];
              let accObject = {};
              response.data.result.forEach((game)=>{
                  accObject = {};
                  accObject["id"] = game.pk
                  accObject["index"] = game.pk
                  accObject["name"] = game.fields.name
                  accObject["usage_type"] = game.fields.usage_type
                  accObject["price"] = parseFloat(game.fields.price) 
                  accObject["image"] = game.fields.image
                  accArr.push(accObject)
              })
              this.setState({
                accessories : accArr
              })
          }else{
              this.messageDialog("Could not update the details.");
          }
      });
  }

  addAccessoriesIndex = (selectedItem) =>{
      let accessories = this.state.selectedAccessories;
      let index = -1;
      accessories.forEach((data, indexVal)=>{
          if(data == selectedItem){
            index = indexVal;
          }
      })
      if(index == -1){
        accessories.push(selectedItem);
      }else{
        accessories.splice(index,1)
      }
      console.log(accessories)
      this.setState({
        selectedAccessories : accessories
      })
  }

  moveToShipping = (finalPrice) =>{
    this.setState({
        finalPrice: finalPrice,
        renderShipping : true
    })
  }

  renderAddAccessories = () => {
    if(this.state.renderShipping){
      return (<Redirect to={{
                  pathname: '/shipping',
                  state: { data:  this.state }
              }} />)
    }
  }
  

  render() {
    const sampleDiv = {
        width : "100%",
        height : "60%",
    }
    
    let listAccessories = [];
    let selected = false;
    this.state.accessories.forEach((data)=>{
        selected = false;
        if(this.state.selectedAccessories.indexOf(data.index)!=-1){
            selected = true;
        }
        listAccessories.push(
            <div className="acc-card-div">
                <AccessoryCard
                    data = {data}
                    selected = {selected}
                    click = {()=>{this.addAccessoriesIndex(data.index)}}
                />
            </div>
        )
    })

    //Get selected software
    let selectedAccIndex = [];
    let currSoftInex = ""
    let finalPrice =  0;
    if( isNaN(this.state.finalPrice) ||  this.state.finalPrice == undefined ){
      finalPrice = 0;
    }else{
      finalPrice = parseFloat(this.state.finalPrice);
    }

    this.state.selectedAccessories.forEach((softIndex)=>{
      this.state.accessories.forEach(data=>{
          if(data.index == softIndex){
            currSoftInex = data
          }
      })
      
      if(currSoftInex.price == 'Free'){
        finalPrice += 0
      }else{
        finalPrice += currSoftInex.price
      }
      selectedAccIndex.push(
            <div className="full-width">
                  <div className="col-sm-7">{currSoftInex.name}</div>
                  <div className="col-sm-4">Rs. {currSoftInex.price}</div>
            </div>
      )     
    })
    return (
      <div className="accessories-main">
          {this.renderAddAccessories()}
          <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF" 
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
          <Dialog ref={(el) => { this.dialog = el }} />
          <div className="acc-header">
            Here's some <span className="bold-highlight">Accessories</span> we think will work <span className="italics-highlight"><i>perfectly</i></span> with your new desktop
          </div>
          <div className="accessories-data">
              <div className="col-sm-1 full-height in-mobile-disappear">

              </div>
              <div className="col-sm-7 full-height">
                    {listAccessories}
              </div>
              <div className="col-sm-4 price-div-height">
                    <div style={sampleDiv} className="in-mobile-disappear">

                    </div>
                    <div className="acc-pricing-div">
                    <div className="full-width full-height">
                            <div className="acc-item-data-div">
                                <div className="full-width">
                                        <div className="col-sm-7"> Custom PC x 1</div>
                                        <div className="col-sm-4">Rs. 
                                        { 
                                        (isNaN(this.state.totalPrice) || this.state.totalPrice == undefined)?
                                        0:
                                        this.state.totalPrice
                                        }</div>
                                </div>
                                {selectedAccIndex}                 
                            </div>
                            <div className="acc-final-price-div">
                                <div className="full-width full-height">
                                    <div className="col-sm-7">
                                        <div className="final-price-acc">Rs. {finalPrice}</div>
                                        <div className="acc-shipping-detail">Free shipping</div>
                                    </div>
                                    <div className="col-sm-4 full-height">
                                            <div onClick={()=>{this.moveToShipping(finalPrice)}} className="acc-next-button-style">
                                                Next
                                            </div>
                                    </div>
                                </div>  
                            </div>
                        </div>
                    </div>
              </div>
          </div>
      </div>
    )
  }
}
export default AddAccessories;