import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Switch from '@material-ui/core/Switch';
import Radio from '@material-ui/core/Radio';
import agent from '../../services/agent.js';
import Dialog from 'react-bootstrap-dialog'
import Loader from 'react-loader-spinner';
import { Redirect } from 'react-router-dom';
import "./Shipping.css";

const DESKTOP = 1;
const MOBILE = 2;

class Shipping extends Component {
  constructor(props){
        super(props);

        const screenWidth = window.screen.width;
        let deviceType = DESKTOP;
        if( parseInt(screenWidth) <= 424 ){
          deviceType = MOBILE
        }else{
          deviceType = DESKTOP
        }

        console.log("Data from selection : ",this.props.location.state.data);
        this.state = {...this.props.location.state.data};
        this.state.selected = 1;
        this.state.personalSelected = true;
        this.state.redirectToSoftwareSelect = false;
        this.state.boxSelected = false;
        this.state.deviceType = deviceType;
        this.state.addressArr = []
        this.state.selectedAddressIndex = 0

        //redirection to transaction
        this.state.redirectToTransaction = false

        //To check if any issue with payment 
        this.state.orderId = null

        //to update the payment details
        this.state.paymentId = null

        //To keep a check that address is added
        this.state.addressAdded = false
        
        //Add blank field
        this.state.name = ""
        this.state.email = ""
        this.state.house = ""
        this.state.city = ""
        this.state.state = ""
        this.state.country = "India"
        this.state.gstin = ""
        this.state.pin = ""

    }

    moveToTransaction = () => {
      this.setState({
        redirectToTransaction : true
      })
    }

    renderTransaction = () => {
      if(this.state.redirectToTransaction){
        return (<Redirect to={{
                    pathname: '/my-orders',
                    state: { data:  this.state }
                }} />)
      }
    }

    handleEdit = (e, type) => {
      if(type == 'name'){
        this.setState({
          name : e.target.value
        })
      }else if(type == 'house'){
        this.setState({
          house : e.target.value
        })
      }else if(type == 'city'){
        this.setState({
          city : e.target.value
        })
      }else if(type == 'state'){
        this.setState({
          state : e.target.value
        })
      }else if(type == 'pin'){
        this.setState({
          pin : e.target.value
        })
      }else if(type == 'gstin'){
        this.setState({
          gstin : e.target.value
        })
      }
    }

    saveOrder = () => {
      //Here order saving will be done
      this.initOverlay("Saving the order data");

      //Get the data for games, software and accessories
      let obj = {}, gameArr = [], softArr = [], accArr = [];
      //Software
      this.state.softwareArr.forEach((data, index)=>{
        if(this.state.selectedSoftareIndex.indexOf(index)!=-1){
          obj = {};
          obj["name"] = data.name
          obj["price"] = data.price
          softArr.push(obj)
        }
      })
      //Games
      this.state.gamesArr.forEach((data, index)=>{
        let selected = false;
        this.state.selectedGamesIndex.forEach((indexVal)=>{
          if(indexVal == data.index){
            selected = true
          }
        })
        if(selected){
          obj = {};
          obj["name"] = data.name
          obj["price"] = data.price
          gameArr.push(obj)
        }
      })
      //Accessories
      this.state.accessories.forEach((data, index)=>{
        let selected = false;
        this.state.selectedAccessories.forEach((indexVal)=>{
          if(indexVal == data.index){
            selected = true
          }
        })
        if(selected){
          obj = {};
          obj["name"] = data.name
          obj["price"] = data.price
          accArr.push(obj)
        }
      })
      const OSArray = [
        "Windows 10 Pro OEM Activated",
        "Ubuntu 18.04 LTS",
        "Dual Boot Win 10 + Ubuntu 18.04"
      ]
      agent.ORDER.saveOrder(
        this.state.userId,
        this.state.finalPrice,
        this.state.platformArr[this.state.currentPlatformIndex].fields.name,
        this.state.processorArr[this.state.curretProcessorIndex].fields.name,
        this.state.motherboardArr[this.state.currentMotherboardIndex].fields.name,
        this.state.ramSizeArr[this.state.cuurentRamSize].fields.name + " " + this.state.ramFreqArr[this.state.currentRamFreqIndex].fields.name ,
        this.state.graphicCardArr[this.state.currentGraphicIndex].fields.name,
        this.state.smpsArr[this.state.currentSMPSIndex].fields.name,
        this.state.PCCaseArr[this.state.currentPCCaseIndex].fields.name,
        this.state.cpucoolerArr[this.state.currentCpucoolerIndex].fields.name,
        this.state.storageArr[this.state.currentStorageIndex].fields.name,
        this.state.storageSecondArr[this.state.currentSecondStorageIndex].fields.name,
        this.state.wifiArr[this.state.currentWifiIndex].fields.name,
        "BLUETOOTH 4.2",
        this.state.micEnabled?1:0,
        this.state.usbcEnabled?1:0,
        OSArray[this.state.selectedOSIndex],
        this.state.filterDataSelection.form == 1? "Mid Tower": "Full Tower",
        this.state.addressArr[this.state.selectedAddressIndex].pk,
        JSON.stringify(gameArr),
        JSON.stringify(accArr),
        JSON.stringify(softArr))
        .then(response => {
            this.endOverlay();
            console.log(response.data);
            if(response.data.status == 1){
                //alert(response.data.result)
                this.setState({
                  orderId : response.data.result
                },()=>{
                  //Once the address details are added now, go for order data update
                  this.proceedForPayment();
                })
            }else{
              this.setState({
                addressArr : []
              })
            }
        });
    }

    savePaymentDetails = (status) => {
        this.initOverlay("Updating the payment details");
        agent.ORDER.updatePaymentStatus(this.state.paymentId, this.state.orderId, status)
        .then(response => {
            this.endOverlay();
            console.log(response.data);
            if(response.data.status == 1){
                this.alertDialog("Your order has been placed");
                this.moveToTransaction();
            }else{
              this.alertDialog("Could not complete your transaction. In case your payment was deducted please cotact at +91 85273 89144");
              this.setState({
                addressArr : []
              })
            }
        });
    }

    endOverlay = () => {
      if(this.state.overlay){
        this.setState({overlay:false})
        document.getElementById("overlay").style.display = "none";
      }
    } 

    initOverlay = (message) => {
        if(!this.state.overlay){
          this.setState({overlay:true})
          document.getElementById("loader-message").innerText = message
          document.getElementById("overlay").style.display = "block";
        }
    }

    proceedForPayment = () => {
        
        if(!this.state.addressAdded){
          //Check if address has been updated
          this.addNewAddress();
        }else if(this.state.orderId == null){
          //Check if order has been updated
          this.saveOrder()
        }else{
          //Go for the openCheckout Payment box
          this.openCheckout();
        }
    }

    componentDidMount = () => {
      this.initOverlay("Fetching the addresses");
      this.fetchAddress();
    }

    fetchAddress = () => {
      agent.Auth.fetchAddress(this.state.userId)
        .then(response => {
            this.endOverlay();
            console.log(response.data);
            if(response.data.status == 1){
                let addressAddess = false;
                if(response.data.result.length > 0){
                  addressAddess = true
                }
                this.setState({
                  addressArr : response.data.result,
                  name : response.data.user_name,
                  addressAdded : addressAddess
                })
            }else{
              this.setState({
                addressArr : []
              })
            }
        });
    }

    addNewAddressDialog = () => {
      this.setState({
        addressAdded : false
      })
    }

    addNewAddress = () => {

      if(this.state.name == "" || this.state.house == "" || this.state.city == "" || this.state.state == ""
        || this.state.country == "" || this.state.pin == ""){
            this.alertDialog("Empty Field","Please enter all address details to continue");
      }else{
          this.initOverlay("Updating the address")
          agent.Auth.addAddress(
            this.state.userId,
            this.state.name,
            this.state.house,
            this.state.city,
            this.state.state,
            this.state.country,
            this.state.pin
          ).then(response => {
              this.endOverlay();
              console.log(response.data);
              if(response.data.status == 1){
                  let addressAddess = false;
                  if(response.data.result.length > 0){
                    addressAddess = true
                  }
                  //Also when a new address is added, last one is the selected address
                  let addressIndex = 0;
                  if(response.data.result.length > 0){
                    addressIndex = response.data.result.length - 1;
                  }
                  this.setState({
                    addressArr : response.data.result,
                    name : response.data.user_name,
                    addressAdded : addressAddess,
                    selectedAddressIndex : addressIndex
                  },()=>{
                    //Once the address details are added now, go for order data update
                    this.proceedForPayment();
                  })
              }else{
                this.setState({
                  addressArr : []
                })
              }
          });
      }
    }

    alertDialog = (title,body) => {
        Dialog.setOptions({
            defaultOkLabel: 'Ok'
        })
        this.dialog.show({
            title: title,
            body: body,
            actions: [
            Dialog.OKAction(
                (el) => {

                }
            )
            ],
            bsSize: 'small',
            onHide: (dialog) => {
            dialog.hide()
            }
        })
    }

    openCheckout = () => {
      const amount =   parseInt(parseFloat(this.state.finalPrice)*100); 
      const address = this.state.house +" ," +this.state.city + " ," + this.state.state + " ," + this.state.country;
      let options = {
          "key": "rzp_live_3cQV2gJDSE9N4f",
          "amount":  500, //amount, // 2000 paise = INR 20, amount in paisa
          "name": "MakeMyRig",
          "description": "System purchase from MakeMyRig",
          "image": "/your_logo.png",
          "handler": (response) => {
            this.setState({
              paymentId : response.razorpay_payment_id
            },()=>{
              console.log(response)
              this.savePaymentDetails(1);
            })
            //alert(response);
            // alert(response.razorpay_payment_id);
          },

          "prefill": {
            "name": this.state.name,
            "email": this.state.email,
            "contact" : this.state.mobile,
          },
          "notes": {
            "address": address
          },
          "theme": {
            "color": "#00e3c5"
          }
      }
      let rzp = new window.Razorpay(options);
      rzp.open();
    }

    handleBoxChange = () =>{
      if(this.state.boxSelected){
          this.setState({
              boxSelected : false
          })
      }else{
        this.setState({
          boxSelected : true
          })
      }
  }

    handleViewChange = () =>{
        if(this.state.personalSelected){
            this.setState({
              personalSelected : false
            })
        }else{
          this.setState({
              personalSelected : true
            })
        }
    }

    handleRadio = (val) => {
        if(val == 'y'){
          this.setState({
              boxSelected : true
          })
        }else{
          this.setState({
              boxSelected : false
          })
        }
      
    }

    selectAddress = (index) => {
      this.setState({
        selectedAddressIndex : index
      })
    }

  render() {
    const nextButtonStyle = {
        width: "100%",
        height : "100%",
        borderRadius : "25px",
        background : "#4CAF50",
        textAlign : "center",
        fontSize : "1.7vw",
        fontWeight : "bold",
        paddingTop : "5%",
        color : "white",
        cursor: "pointer",
    }
    let gstin = {
        display : "block"
    }
    if(!this.state.personalSelected){
        gstin.display = "none"
    }
    let checkoutDiv = <div className="footer-form">
                            <div className="footer-start">

                            </div>
                            <div className="footer-details">
                                
                            </div>
                            <div className="next-button">
                                <div onClick={this.proceedForPayment} style={nextButtonStyle}>
                                    Checkout
                                </div>
                            </div>
                        </div>
    if(this.state.deviceType== MOBILE){
        checkoutDiv = <div className="checkout-btn-div">
                            <div onClick={this.proceedForPayment} className="ship-next-button-style">
                                    Checkout
                            </div>
                        </div>
    }

    //Final price listing
    //Software
    //Get selected software
    let selectedAccIndex = [];
    let currAccInex = ""
    //let finalPrice =  parseFloat(this.state.finalPrice);

    this.state.selectedAccessories.forEach((softIndex)=>{
      this.state.accessories.forEach(data=>{
          if(data.id == softIndex){
            currAccInex = data
          }
      })
      // currAccInex = {...this.state.accessories[softIndex]};
      selectedAccIndex.push(
            <div className="full-width order-data">
                  <div className="width-60">{currAccInex.name}</div>
                  <div className="width-40">Rs. {currAccInex.price}</div>
            </div>
      )     
    }) 

    //Get selected software
    let selectedSoftwareIndex = [];
    let currSoftInex = ""
    this.state.selectedSoftareIndex.forEach((softIndex)=>{
      currSoftInex = {...this.state.softwareArr[softIndex]}
      selectedSoftwareIndex.push(
        <div className="full-width order-data">
                  <div className="width-60">{currSoftInex.name}</div>
                  <div className="width-40">Rs. {currSoftInex.price}</div>
            </div>
      )     
    })

    if(this.state.microsoftSelected){
      selectedSoftwareIndex.push(
        <div className="full-width order-data">
              <div className="width-60">{"Microsoft Home and Student 2019, Lifetime Validity"}</div>
              <div className="width-40">Rs. {parseFloat(4999.95)}</div>
        </div>
      )   
    }
  

    //Get selected games
    let selectedGamesIndex = [];
    let currGameInex = ""
    this.state.selectedGamesIndex.forEach((softIndex)=>{
      this.state.gamesArr.forEach(data=>{
          if(data.index == softIndex){
            currGameInex = data
          }
      })
      // currGameInex = {...this.state.gamesArr[softIndex]}
      selectedGamesIndex.push(
        <div className="full-width order-data">
                  <div className="width-60">{currGameInex.name}</div>
                  <div className="width-40">Rs. {currGameInex.price}</div>
            </div>
      )     
    })

    //Adding the shipping address
    let addressEntry = [];
    let addressArray = [];
    let displayDiv = ""
    addressEntry.push(
            <div className="full-width text-block">
                <input value={this.state.name} onChange = {(e)=>this.handleEdit(e,'name')} type="text" className="text-field-data" placeholder="Enter Name" />
            </div>
    )
    addressEntry.push(
          <div className="full-width text-block">
                <input value={this.state.house} onChange = {(e)=>this.handleEdit(e,'house')} type="text" className="text-field-data" placeholder="House no./Society" />
            </div>
    )
    addressEntry.push(
      <div className="full-width text-block">
          <input value={this.state.city} onChange = {(e)=>this.handleEdit(e,'city')}  type="text" className="text-field-data" placeholder="City" />
      </div>
    )
    addressEntry.push(
      <div className="full-width text-block">
          <input value={this.state.state} onChange = {(e)=>this.handleEdit(e,'state')}  type="text" className="text-field-data" placeholder="State" />
      </div>
    )
    addressEntry.push(
      <div className="full-width text-block">
            <input value={this.state.pin}  onChange = {(e)=>this.handleEdit(e,'pin')} type="text" className="text-field-data" placeholder="PIN Code"/>
        </div>
    )

    if(this.state.addressArr.length == 0 || this.state.addressAdded == false){
      displayDiv = addressEntry;
    }else{
      this.state.addressArr.forEach((data, index)=>{
        if(this.state.selectedAddressIndex == index){
          addressArray.push(
            <div onClick={()=>this.selectAddress(index)} className="selected-adress-box">
              <div>{this.state.name}</div>
              <div>{data.fields.house_num}, {data.fields.society}</div>
              <div>{data.fields.city}- {data.fields.pin}</div>
              <div>{data.fields.state}</div>
            </div>
          )
        }else{
          addressArray.push(
            <div onClick={()=>this.selectAddress(index)} className="adress-box">
              <div>{this.state.name}</div>
              <div>{data.fields.house_num}, {data.fields.society}</div>
              <div>{data.fields.city}- {data.fields.pin}</div>
              <div>{data.fields.state}</div>
            </div>
          )
        }
        
      })
      displayDiv = <div className="address-present">
                        
                        {addressArray}
                    </div>
    }

    return (
      <div className="ship-main">
        {this.renderTransaction()}
          <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF" 
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
          <Dialog ref={(el) => { this.dialog = el }} />
           <div className="ship-header">
                Add your shipping details to proceed
           </div>
           
           <div className="ship-selection-div">
              <div className="canvas-upload-info">
                  <div>You can upload canvas image for your splash screen here</div>
                  <div><input type="file"></input></div>
              </div>
                <div className="col-sm-5 address-block-height">
                    {
                      this.state.addressAdded?
                        <div onClick={()=>this.addNewAddressDialog()} className="add-adress-box">
                          Add New Address
                        </div> :
                        ""
                    }
                    {displayDiv}
                    <div className="full-width text-block">
                        <span className={` ${ (this.state.personalSelected)? "view-change-selected":"view-change-unselected"}`}>Personal</span>
                            <Switch
                                checked={this.state.personalSelected}
                                color="default"
                                inputProps={{ 'aria-label': 'checkbox with default color' }}
                                onChange={()=>this.handleViewChange()}
                                className="switch-class"
                                />
                        <span className={` ${ (!this.state.personalSelected)? "view-change-selected":"view-change-unselected"}`}>Business</span>
                    </div>
                    <div style={gstin} className="full-width text-block">
                        <input value={this.state.gstin}  onChange = {(e)=>this.handleEdit(e,'gstin')}  type="text" className="text-field-data" placeholder="GSTIN" />
                    </div>
                    <div className="full-width text-block">
                        <div className="col-name-div">Ship original Boxes</div>
                        <div className="col-op-div">
                            <span>Yes</span> <input className="radio-btn" onClick={()=>this.handleRadio('y')} type="radio" value="yes" checked={this.state.boxSelected} />
                            <span style={{"marginLeft":"10%"}}>No</span> <input className="radio-btn"  onClick={()=>this.handleRadio('n')} type="radio" value="no" checked={!this.state.boxSelected} />
                        </div>
                    </div>
                   
                    
                </div>
                <div className="col-sm-7 full-height">
                    <div className="order-summary-box">
                        <div className="full-width summary-header">Order Summary</div>
                        <div className="order-detail-div">
                            <div className="full-width order-data">
                                <div className="width-60">Custom PC x 1</div>
                                <div className="width-40">Rs. {this.state.totalPrice}</div>
                            </div>
                            {selectedAccIndex}
                            {selectedSoftwareIndex}
                            {selectedGamesIndex}
                        </div>
                        <div className="divider-line"></div>
                        <div className="order-total-div">
                            <div className="full-width">
                                <div className="width-60">Total</div>
                                <div className="width-40">Rs. {this.state.finalPrice}</div>
                            </div>
                        </div>
                    </div>
                </div>
                    

           </div>
           {checkoutDiv}
      </div>
    )
  }
}
export default Shipping;