const Auth = {
	sendOtp : (mobile) => {
		return userRequests.post('send_otp/',
		 	qs.stringify({
				"mobile": mobile
			})
		 );
	},
	verifyOtp : (mobile,otp) => {
		return userRequests.post('verify_otp/',
		 	qs.stringify({
				"mobile": mobile,
				"otp":otp,
			})
		 );
	},
	fetchAddress : (uid) => {
		return userRequests.post('fetch_address/',
		 	qs.stringify({
				"uid": uid,
			})
		 );
	},
	addAddress : (uid,name,house,city,state,country,pin) => {
		return userRequests.post('save_address/',
		 	qs.stringify({
				"uid": uid,
				"name" : name,
				"house" :house,
				"city" :city,
				"state" : state,
				"country" : country,
				"pin" : pin
			})
		 );
	},
}

