import React, { Component } from 'react';
import SoftwareCard from "../SoftwareCard/SoftwareCard.jsx";
import SoftwareLargeCard from "../SoftwareLargeCard/SoftwareLargeCard.jsx";
import { Redirect } from 'react-router-dom';
import GameBox from "./../GameBox/GameBox.jsx";
import agent from '../../services/agent.js';
import Dialog from 'react-bootstrap-dialog'
import Loader from 'react-loader-spinner'
import "./AddGames.css";

export default class AddGames extends Component {
    constructor(props){
        super(props);
        console.log("Data from selection : ",this.props.location.state.data);
        this.state = {...this.props.location.state.data};
        this.state.selectedGamesIndex = []
        this.state.renderAddAccessories = false;
        this.state.gamesArr = [ ]
    }

    endOverlay = () => {
        if(this.state.overlay){
            this.setState({overlay:false})
            document.getElementById("overlay").style.display = "none";
        }
    } 

    initOverlay = (message) => {
        if(!this.state.overlay){
            this.setState({overlay:true})
            document.getElementById("loader-message").innerText = message
            document.getElementById("overlay").style.display = "block";
        }
    }

    componentDidMount = () => {
        this.initOverlay("Fetching the games data");
        this.fetchAllGames();
    }

    selectGames = (selectedItem) => {
        let softwareIndexArr = this.state.selectedGamesIndex;
        let index = -1;
        softwareIndexArr.forEach((data, indexVal)=>{
            if(data == selectedItem){
                index = indexVal;
            }
        })
        if(index == -1){
            softwareIndexArr.push(selectedItem);
        }else{
            softwareIndexArr.splice(index,1)
        }
        this.setState({
            selectedGamesIndex : softwareIndexArr
        },()=>{
            //Check the data updates
            console.log(this.state)
        })
    }

    fetchAllGames = () => {
        agent.PCConfig.listGames()
        .then(response => {
            this.endOverlay();
            if(response.data.status == 1){
                let accArr = [];
                let accObject = {};
                response.data.result.forEach((game)=>{
                    accObject = {};
                    accObject["id"] = game.pk
                    accObject["index"] = game.pk
                    accObject["name"] = game.fields.name
                    accObject["fps"] = game.fields.fps
                    accObject["price"] = 0
                    accObject["image"] = game.fields.image
                    accArr.push(accObject)
                })
                this.setState({
                    gamesArr : accArr
                })
            }else{
                this.messageDialog("Could not update the details.");
            }
        });
    }

    moveToAddAccessories = (finalPrice) =>{
        this.setState({
            finalPrice : finalPrice,
            renderAddAccessories : true
        })
    }

    renderAddAccessories = () => {
        if(this.state.renderAddAccessories){
            return (<Redirect to={{
                        pathname: '/add-accessories',
                        state: { data:  this.state }
                    }} />)
        }
    }

    render() {
    let softwareList = [];
    let selected = false;
    this.state.gamesArr.forEach((data)=>{
        selected = false;
        if( this.state.selectedGamesIndex.indexOf( data.id ) != -1 ){
            selected = true
        }
        softwareList.push(
            <div key={data.id} className="col-xs-2 game-box">
                <GameBox
                    id = {data.id}
                    name = {data.name}
                    image = {data.image}
                    selected = {selected}
                    onSelect = {()=>this.selectGames(data.id)}
                />
            </div>
        )
    })
    let microsoftSelected = false;
    

    //Get selected software
    let selectedSoftwareIndex = [];
    let currSoftInex = ""
    let finalPrice =  parseFloat(this.state.totalPrice);
    this.state.selectedGamesIndex.forEach((softIndex)=>{
        this.state.gamesArr.forEach(data=>{
            if(data.id == softIndex){
              currSoftInex = data
            }
        })
        if(currSoftInex.price == 'Free' || currSoftInex.price == 0){
            finalPrice += 0
        }else{
            finalPrice += currSoftInex.price
        }
        selectedSoftwareIndex.push(
            <div className="full-width">
                    <div className="col-sm-7">{currSoftInex.name}</div>
                    <div className="col-sm-4">Rs. {currSoftInex.price}</div>
            </div>
        )     
    })

    return (
        <div className="accessories-main">
            {this.renderAddAccessories()}
            <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF" 
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
            </div>
            <Dialog ref={(el) => { this.dialog = el }} />
            <div className="greet-header">
            Here are some games we think you will like! 
            </div>
            <div className="greet-header">
            Just pick what you like and we'll install them for you, free of charge!
            </div>
            <div className="games-display">
            {softwareList}
            </div>
            <div className="pricing-div">
                <div className="software-lg-div full-height">
                   
                </div>
                <div className="pricae-container-div full-height">
                    <div className="item-data-div">
                        <div className="full-width">
                            <div className="col-sm-7">Custom PC x 1</div>
                            <div className="col-sm-4">Rs. {this.state.totalPrice}</div>
                        </div>
                        {selectedSoftwareIndex}                
                    </div>
                    <div className="final-price-div">
                        <div className="full-width full-height">
                            <div className="col-sm-7">
                                <div className="final-price">Rs. {finalPrice}</div>
                                <div className="shipping-detail-div">Free shipping</div>
                            </div>
                            <div className="col-sm-4 full-height">
                                    <div onClick={()=> {this.moveToAddAccessories(finalPrice)}} className="next-button-style">
                                    Next
                                    </div>
                            </div>
                        </div>  
                    </div>
                </div>
            </div>
        </div>
    )
    }
    
}
